import React, { Component } from 'react'
import logo from './logo.svg';
import './App.css';
import './components/RobotImage.css';
import RobotImage from './components/RobotImage';
import NameBox from './components/NameBox';
import ButtonGenerate from './components/ButtonGenerate';
import { FacebookShareButton } from 'react-share';
import { FacebookIcon } from 'react-share';

class App extends Component {
  constructor(){
    super()
    this.state={
      input:''

    }
 
  }

onNameBoxChange=(event)=>{
  this.setState({input:event.target.value});

}
onClickGenerate=(event)=>{
  console.log(this.state.input);
  let imgSrc=`https://robohash.org/${this.state.input}`;
  console.log(imgSrc);  
}
  
  render(){
   const { classes } = this.props;
    return (
    <div className="App tc">
      <h1>My Robot Friend</h1>
      <NameBox inputChange={this.onNameBoxChange}/>
      <ButtonGenerate Generate={ this.onClickGenerate }/>
      <RobotImage source={`https://robohash.org/${this.state.input}`} NameBoxValue={this.state.input}/>
      <div className='cShare'>
        <p>Share your robot Now</p>
      </div>
        <FacebookShareButton
             url={`https://robohash.org/${this.state.input}`}
             quote={"Here I'm, When I looks like a robot. See yourself too... "+'http://myrobotfriend.com'}
             hashtag="#myrobotfriend"
             className='fbShare'
           >
         <FacebookIcon size={36} round={true}/> 
       </FacebookShareButton>

    </div>

  );

  }
}

export default App;
