import React from 'react';

const RobotImage =({NameBoxValue,source})=>{
	if (!NameBoxValue){
		return(
			<div>
				<img alt='logo' src='https://i.ibb.co/4W7BVCx/d4ecb2ff-f75f-47ae-96e5-7395eb67984c-200x200.png' />
			</div>
		);
	} else {
		return(
					
			<div>

				<img alt='robot image here' src={`${source}`} />
				
			</div>
		);
	}

}
export default RobotImage;